@keyframes pulsate {
	50% {
		box-shadow: inset 0 0 50px #fff, inset -50px 0 80px #f0f, inset 50px 0 80px #0ff,
			inset -50px 0 300px #f0f, inset 50px 0 300px #0ff;
	}
}

.virtual-layer {
	overflow: hidden;
	/* animation: pulsate 6s linear infinite; */
	background-color: rgba(75, 154, 244, 0.7);
	border-radius: 0.25rem;
}

.virtual-layer-content {
	display: flex;
	justify-content: center;
	align-items: center;
	color: darkblue;
	font-weight: 500;
}
